
import { ConnectButton, darkTheme } from '@rainbow-me/rainbowkit';
import React, { useEffect, useState} from 'react';
import "./info.scss"
import {saveWaitlist} from 'jacker/save.js';
import {connect} from 'react-redux';
import { useAccount } from 'wagmi';
import { useSigner } from 'wagmi';

const WAITLIST_MESSAGE_TO_SIGN = `
I would like to join the waitlist for Spores.
`;

const History = ({clear}) => { 
    const {data: account} = useAccount();
    const {data: signer } = useSigner();

    useEffect(() => {
        if (account && signer) {
            // we've connected wallet
            // check if already on waitlist
            if (window.localStorage.getItem("waitlist-message")) {
                // already joined
                return;
            }

            let walletAddress = account.address;

            // have user sign a message
            signer.signMessage(WAITLIST_MESSAGE_TO_SIGN).then(
                x => {
                    window.localStorage.setItem("waitlist-message", x);

                    // save the wallet address & message
                    saveWaitlist(walletAddress, x);
                });

        }
    }, [account, signer]);

    return (
        <div className="infoContainer" onClick={() => { clear(); }}>
          <div
            onClick={e => e.stopPropagation()}
            className="infoBox">
              <a className="go-to-landing" href="/">
                <img
                  className="spores-logo-black" src="/spores-logo-black.svg"/></a>
              <p><i>Spores</i> is a new way to stream and personalize music NFTs. </p>
              <p>Grab your favorite music NFT from any platform and play with it here until limited time access is up, or <a href='/buy' className='buyLink'>buy your own unlimited <i>Spore</i></a> where we beam curated songs from the future every week.</p>
              <div
                className="join-waitlist-container">
                <ConnectButton
                  accountStatus="address" 
                  showBalance={{ smallScreen: false, largeScreen: false }}
                  label="Connect Wallet"
                /> </div>
            </div>
       </div>
    );
};

const mapStateToProps = (state, props) => {
  return {
  };
};

const mapDispatchToProps = (dispatch) => {
  return {
  };
};

export default connect(mapStateToProps, mapDispatchToProps)(History);
