const History = ({latestJacks, loadHistory}) => { 
    const load = useCallback((x) => {
        if (x.metadata) {
            loadHistory(x.metadata);
        } else {
            ipfs.getJson(x.data).then(
                y => loadHistory(y));
        }
    }, [loadHistory]);

    let history = [... KEYON_SONGS, ... latestJacks];
    return (<div className="jack-history">
              <img className="globe" src="/globe.svg"/>
              <div className="items">
                {history
                 .filter(x => x.title && x.title.trim() !== "") 
                 .map(
                  x => <div
                         onClick={() => load(x)}
                         className={x.isSporeTrack ? "history-item spore-track" : "history-item"}>
                         {x.title}
                         {x.isSporeTrack && <img src="/spores-icon-glow2.svg"/>}
                       </div>)}

              </div>
            </div>);
};

const mapStateToProps = (state, props) => {
  return {
  };
};

const mapDispatchToProps = (dispatch) => {
  return {
  };
};

export default connect(mapStateToProps, mapDispatchToProps)(History);
