import React, { useEffect, useCallback, useState} from 'react';
import PlayerHelper from 'components/spore/PlayerHelper.js';
import History from './History.js';
import Info from './Info.js'
import { ConnectButton, lightTheme} from '@rainbow-me/rainbowkit';
import JackerPlayer from './JackerPlayer.js';
import {jack} from 'jacker/jack.js';
import {save} from 'jacker/save.js';
import {getArtistFromMetadata} from 'jacker/artist.js';
import {connect} from 'react-redux';
import './nft-jacker.scss';
import {useTokenMetadata} from 'components/hooks/useTokenMetadata.js';
import {submitSplitJob} from 'jacker/stem-splitter.js';
import { prominent } from 'color.js';

const NFTJacker = (props) => { 
    const [link, setLink] = useState('');
    const [showHelp, setShowHelp] = useState(false);
    const [currentLink, setCurrentLink] = useState();
    const [latestJacks, setLatestJacks] = useState();
    const [showHistory, setShowHistory] = useState(false);
    const [showInfo, setShowInfo] = useState(false);
    let [showLinkInput, setShowLinkInput] = useState(true);
    let [showDesc, setShowDesc] = useState(true);
    let [jackedAudio, setJackedAudio] = useState();
    let [artist, setArtist] = useState();
    let [coverArt, setCoverArt] = useState();
    let [songName, setSongName] = useState();
    let [description, setDescription] = useState();
    let getTokenMetadata = useTokenMetadata();
    let [showPlayer, setShowPlayer] = useState();
    let [error, setError] = useState();
    let [coverGrad, setCoverGrad] = useState();
    let [webKitGrad, setWebKitGrad] = useState();
    let [mozGrad, setMozGrad] = useState();

    useEffect(() => {
        let baseURL = window.location.hostname === "localhost" ? "http://localhost:9090/latestJacks/" : "https://zequencer.io/latestJacks/";
        baseURL = "https://zequencer.io/latestJacks/";
        fetch(baseURL).then(
            async ret => {
                let json = await ret.json();
                setLatestJacks(json);
            });
    }, []);

    const clear = useCallback(() => {
        setLink("");
        setShowPlayer(false);
        setCoverArt(undefined);
        setArtist(undefined);
        setSongName(undefined);
        setDescription(undefined);
        setJackedAudio(undefined);
        setShowLinkInput(true);
    }, [
        setShowLinkInput,
        setShowPlayer,
        coverArt,
        setLink,
        setArtist,
        setSongName,
        setDescription,
        setJackedAudio
    ]);

    const onJack = useCallback(async () => {
        if (link === "") {
            return;
        }
        setShowPlayer(true);
        setShowLinkInput(false);
        setShowDesc(false);
        let ret = await jack(link, getTokenMetadata);
        if (!ret) {
            clear();
            setError("The token you\'ve linked has a strange format. NFT Jacker rejects it, respectfully...");
            return;
        }
        ret.link = link;
        loadJack(ret, true);
    }, [
        link,
        latestJacks,
        setSongName,
        setShowHistory,
        setJackedAudio,
        setArtist,
        setShowPlayer,
        setError,
        setShowLinkInput,
        setCurrentLink
    ]);

    const loadJack = useCallback((ret, shouldSave) => {
        setShowHistory(false);
        setShowPlayer(true);
        setShowLinkInput(false);
        setShowDesc(false);
        
        let {url, ipfs} = ret;

        if (!url) {
            clear();
            setError("The token you\'ve linked might not contain a music NFT.");
            return;
        }

        setError(undefined);

        let toSave = {};
        if (url.startsWith("ipfs://")) {
            ipfs = url.slice(7);
        }
        if (ipfs) {
            let url = "https://zequencer.io/ipfs/" + ipfs;
            submitSplitJob(url);
            setJackedAudio(ipfs);
            toSave.ipfs = ipfs;
        } else {
            if (url.startsWith("ipfs://")) {
                url = "https://zequencer.io/ipfs/" + url.slice(7);
            }
            setJackedAudio(url);
            toSave.url = url;
        }
        if (ret.body) {
            ret = {
                ... ret,
                ...ret.body,
                name: ret.body.title
            };
            if (ret.body.artwork && ret.body.artwork.info) {
                ret = {
                    ... ret,
                    image: ret.body.artwork.info.uri
                };
            }
        }
        let artist = getArtistFromMetadata(ret);
        toSave.artist = artist;
        setArtist(artist);
        setSongName(ret.name);
        toSave.song = ret.name;
        setDescription(ret.description);
        let image = ret.image;
        if (ret.image) {
            if (ret.image.startsWith("ipfs://")) {
                image = "https://zora-prod.mypinata.cloud/ipfs/" + image.slice(7);
            } 
            setCoverArt(image);
        }

        toSave = {
            ... ret,
            ... toSave
        };

        if (ret.link) {
            setCurrentLink(ret.link);
        } else {
            setCurrentLink(undefined);
        }

        if (shouldSave) {
            save(toSave);
            setLatestJacks([
                toSave,
                ... latestJacks,
            ]);
        }
    }, [
        setCurrentLink,
        link,
        setLatestJacks,
        latestJacks,
        setSongName,
        setJackedAudio,
        setArtist,
        setShowPlayer,
        setError,
        setShowLinkInput,
        setShowHistory
    ]);

    // cover art background
    useEffect(() => {
      if (coverArt != null) {
        prominent(coverArt, { amount: 3, group: 50, format: "hex"}).then(color => {
          setCoverGrad("linear-gradient(45deg, " + color[0] + " 0%, " + color[1] + " 50%, " + color[2] + " 100%)");
        })
      }
    }, [coverArt]);

    // countdown timer
    const calculateTimeLeft = () => {
      let year = new Date().getFullYear();
      const difference = +new Date(`${year}-06-20`) - +new Date();
      let timeLeft = {};
  
      if (difference > 0) {
        timeLeft = {
          D: Math.floor(difference / (1000 * 60 * 60 * 24)),
          H: Math.floor((difference / (1000 * 60 * 60)) % 24),
          M: Math.floor((difference / 1000 / 60) % 60),
          S: Math.floor((difference / 1000) % 60),
        };
      }
  
      return timeLeft;
    };
  
    const [timeLeft, setTimeLeft] = useState(calculateTimeLeft());
    const [year] = useState(new Date().getFullYear());
  
    useEffect(() => {
      setTimeout(() => {
        setTimeLeft(calculateTimeLeft());
      }, 1000);
    });
  
    const timerComponents = [];
  
    Object.keys(timeLeft).forEach((interval) => {
      if (!timeLeft[interval]) {
        return;
      }
  
      timerComponents.push(
        <div className="interval">
          {timeLeft[interval]}{interval}
        </div>
      );
    });

    let className = showPlayer ? "nft-jacker jacked" : "nft-jacker";
    if (showLinkInput) {
        className += " showing-link-input";
    }

    return (
      <>
        <div
          onClick={() => setShowHistory(false)}
          className={className}
          style={{background: coverGrad}}>
          {showHelp && <PlayerHelper clear={() => setShowHelp(false)}/>}
          {showPlayer && <div onClick={() => setShowHelp(true)} className="helper-icon">?</div>}
        {!showPlayer &&
         <h1>
         NFT JACKER
         </h1>}
          {/*<p className={showDesc ? "desc": "desc hide"}>We've invented a new way to control music. Load any music NFT link and realize a whole new dimension for sound.</p>*/}
          {error && <div className="error-message">{error}</div>}
          <div className={jackedAudio ? "player-container show" : "player-container hide"}>
            <JackerPlayer jackedAudio={jackedAudio}/>
          </div>

          <div className={!showLinkInput ? "nft-link hide" : "nft-link"}>
            <input
              onClick={e => e.stopPropagation()}
              onFocus={() => {
                  setShowHistory(true);
                  setShowInfo(false);
              }}
              placeholder="Drop a link to any music NFT" value={link} onChange={e => {
                  setShowHistory(false);
                  setShowInfo(false);
                  setLink(e.target.value);
              }}/>
            {showHistory && <History loadHistory={loadJack} latestJacks={latestJacks}/>}
            <div onClick={() => onJack()} className="jack-btn">
              Remix
            </div>
          </div>
        {songName && <div className="artist-name">
         {artist}
         <div className="song-name">
         {songName}
         </div>
                       {coverArt && <img className="cover-art" src={coverArt}/>}
                       <div />
                       {currentLink && <a className="current-link" href={currentLink} target="_blank" rel="noreferrer">↗</a>}
                     </div>}
          {showPlayer &&
           <div onClick={(e) => {
               e.stopPropagation();
               setLink("");
               setShowHistory(true);
               setShowLinkInput(true);
           }} className="back-btn">
           </div>}
           <div className="timer">
          {timerComponents.length ? timerComponents : <span>Time's up!</span>}
        </div>
        {showInfo && <Info clear={() => setShowInfo(false)}/>}
        <a onClick={() => setShowInfo(true)} className="stockLeft">What is this?</a>
        </div>
        {/* <video
        playsInline
        className="shroomVideo"
        autoPlay
        loop
        muted
        src={"/mushroom-jacker.mp4"}/> */}
                </>);
};

const mapStateToProps = (state, props) => {
  return {
  };
};

const mapDispatchToProps = (dispatch) => {
  return {
  };
};

export default connect(mapStateToProps, mapDispatchToProps)(NFTJacker);
